import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import Circle from './Circle';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

let perRow = 4;
let startX = WIDTH * 0.3;
let startY = WIDTH * 0.7;
let circleSize = (startY - startX) / perRow;

let total = 5;

const circles = new Array(total).fill(1).map((v, index) => {
  let posX = startX + ((index % (perRow + 1)) * circleSize);
  return new Circle(posX, HEIGHT / 2, circleSize)
});

app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);

  for (let i = 0; i < circles.length; i++) {
    circles[i].render();
  }
});

document.body.addEventListener('keyup', (e) => {
  let idx = parseInt(e.key) - 1;

  if (circles.length > idx) {
    circles[idx].animate();
  }
});

app.start();
